@mixin default__description-and-example {
  display: flex;
  flex-wrap: wrap;

  .styleguide-description {
    @include styleguide-description;
    flex-basis: $text-block-width;
  }

  .example {
    @include example;
    flex: 1;
  }

  @include tablet-and-below {
    flex-direction: column;
    flex-wrap: nowrap;

    .styleguide-description {
      flex-basis: auto;
    }
  }
}
