@mixin intro {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  @include colors(gray-on-black);

  padding: $lem;

  @include desktop {
    padding: $lem * 2 $lem;
  }


  .intro__title {
    @include intro__title;
  }

  .intro__lines {
    @include intro__lines;
  }

  .intro__numbers {
    @include intro__numbers;
  }

  .intro__colors {
    @include intro__colors;
  }

  .intro__description {
    @include intro__description;
  }
}
